import React from 'react';

import api from '../lib/api';
import Headline from '../components/headline/headline';
import Subline from '../components/subline/subline';
import RichText from '../components/richtext/richtext';
import Frame from '../components/frame/frame';
import styles from './blog.css';
import ContactAction from '../components/contactaction/contactaction';

type Props = {
  cta: boolean;
  items: Array<{
    id: number;
    subline: string;
    headline: string;
    content: string;
  }>;
  anschrift: string;
  pageTitle: string;
  pageDescription: string;
};

export default function Blog(data: Props) {
  return (
    <Frame singlePage title="Funkstelle" description={data.pageDescription}>
      <div className={styles.BlogWrap}>
        {data.items.map((i) => (
          <div key={i.headline}>
            <Headline className={styles.BlogHeadline} level={2}>
              {i.headline}
            </Headline>
            <Subline>{i.subline}</Subline>
            <RichText className={styles.BlogRT}>{i.content}</RichText>
            {i.cta && <ContactAction />}
          </div>
        ))}
      </div>
    </Frame>
  );
}

Blog.getInitialProps = () => api('Blog');
